.mainNav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 1.3em;
  z-index: 999; }
  .mainNav ul {
    width: 100%;
    position: absolute;
    top: 0;
    opacity: 0;
    transform: translate3d(0, -100%, 0);
    margin: 0;
    list-style: none;
    padding: .5rem;
    transition: transform .5s, opacity .5s;
    background: #fff;
    font-family: "Montserrat", sans-serif; }
    @media (min-width: 768px) {
      .mainNav ul {
        display: flex;
        justify-content: flex-end;
        transform: none;
        height: 4rem; } }
  .mainNav li {
    display: inline-block;
    text-align: center;
    width: 100%; }
    @media (min-width: 768px) {
      .mainNav li {
        width: auto; }
        .mainNav li.logo {
          margin-right: auto; }
          .mainNav li.logo a {
            padding: 0; } }
  .mainNav a {
    display: block;
    width: 100%;
    height: 100%;
    padding: .5rem 1rem; }
  .mainNav .active {
    color: #777;
    pointer-events: none;
    cursor: default; }
  .mainNav button {
    overflow: hidden;
    margin: 0;
    padding: 0;
    font-size: 0;
    text-indent: -9999px;
    border-radius: 0 0 0 5px;
    border: none;
    mix-blend-mode: difference;
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 1cm;
    height: 1cm;
    transition: background 0.6s; }
    @media (min-width: 768px) {
      .mainNav button {
        display: none; } }
    .mainNav button:focus {
      outline: none; }
    .mainNav button span {
      display: block;
      position: absolute;
      top: 0.4495cm;
      left: 0.2cm;
      right: 0.2cm;
      height: 0.101cm;
      background: #fff;
      border-radius: 5px;
      transition: background 0.3s; }
      .mainNav button span.after, .mainNav button span.before {
        transition-duration: 0.3s, 0.3s; }
      .mainNav button span.before {
        transition-property: top, transform;
        top: 0.2495cm; }
      .mainNav button span.after {
        transition-property: bottom, transform;
        top: auto;
        bottom: 0.2495cm; }
  .mainNav.visible ul {
    opacity: .8;
    transform: translate3d(0, 0, 0); }
  .mainNav.visible button span:not(.before):not(.after) {
    background: none; }
  .mainNav.visible button span.before {
    top: 0.4495cm;
    transform: rotate(-45deg); }
  .mainNav.visible button span.after {
    bottom: 0.4495cm;
    transform: rotate(45deg); }

